import { useSelector, useDispatch } from "react-redux";
import { RootState } from "../store/store";
import { clearCart } from "../store/cartSlice";
import toast from "react-hot-toast";

// Type for a single product
interface Product {
  id: number;
  name: string;
  price: number;
}

// Type for a cart item
interface CartItem {
  quantity: number;
  product: Product;
}

interface Props {
  products: Product[];
}

export default function CartSummary({ products }: Props) {
  const cart = useSelector((state: RootState) => state.cart);
  const dispatch = useDispatch();

  const totalItems = Object.values(cart).reduce(
    (sum, item: CartItem) => sum + item.quantity,
    0
  );

  const totalPrice = Object.values(cart).reduce(
    (sum, item: CartItem) => sum + item.quantity * item.product.price,
    0
  );

  const handleClear = () => {
    dispatch(clearCart());
    toast("Cart cleared", { icon: "üóëÔ∏è" });
  };

  return (
    <div className="relative">
      <div className="flex items-center space-x-2">
        <span className="text-gray-700 font-medium">üõí Cart</span>
        <span className="bg-indigo-600 text-white text-sm font-semibold px-2 py-1 rounded-full">
          {totalItems}
        </span>
      </div>
      <div className="text-sm text-gray-500">
        Total:{" "}
        <span className="font-semibold text-gray-800">
          ${totalPrice.toFixed(2)}
        </span>
      </div>
      <button
        onClick={handleClear}
        className="mt-2 text-xs text-red-500 hover:underline"
      >
        Clear Cart
      </button>
    </div>
  );
}
